.hero .logo
{
    border: 1vw solid #ffffff;
    box-sizing: border-box;
    border-radius: 50%;
    height: 20vw;
    width: 20vw;
    min-height: 256px;
    min-width: 256px;
    animation: 2s fade-in linear;
}

.hero .overlay
{
    height: 100%;
    left: 0;
    min-height: 100vh;
    max-width: 100vw;
    position: absolute;
    top: 0;
    width: 100%;
    z-index: -1;
}

.hero .overlay video
{
    object-fit: cover;
}

.hero .intro
{
    font-family: "Times New Roman", Times, serif;
    font-size: 10vw;
    font-weight: bold;
    animation: 3s fade-in linear;
}

.hero .tagline
{
    font-family: Arial, Helvetica, sans-serif;
    font-size: 5vw;
    font-style: italic;
    animation: 5s fade-in linear;
}

.hero .arrow
{
    height: 5vh;
    width: 5vh;
}

.hero .arrow:first-child
{
    animation: 1s animateArrow 4s linear infinite;
}

.hero .arrow:last-child
{
    animation: 1s animateArrow 4s linear infinite;

}

@media (max-width: 768px){
    .hero .intro
    {
        font-size: 15vw;
    }

    .hero .tagline
    {
        font-size: 10vw;
    }
}
.skills
{
    background-color: var(--bg_color_alt);
    color: var(--color_alt);
}


.skills.layout .content
{
    justify-content: space-around;
    display: block;
    flex-wrap: wrap;
    max-width: 80vw;
    width: 100%;
}

.skills.layout .content table
{
    align-self: flex-start;
    border-radius: 2%;
    flex-basis: 40%;
    flex-grow: 0;
	flex-shrink: 0;
    margin: 2vh 2.5%;
    padding: 0 2.5;
    text-align: left;
    white-space: nowrap;
}

.skills.layout .content table tr th
{
    text-transform: uppercase;
}

.skills.layout .content table tr td:first-child
{
    min-width: 200px;
    text-transform: capitalize;
}

.skills.layout .content table tr td:last-child::after
{
    content: ' %';
}


:root
{
    --bg_color: #0D1117;
    --bg_color_alt: #161B22;
    --color: #ededed;
    --color_alt: #eeeeee;
}
@keyframes animateArrow
{
    0%, 100%
    {
       transform: translateY(-1vh);
    }
    25%, 75%
    {
        transform: translateY(0vh);
    }
    50%
    {
       transform: translateY(1vh)
    }
}

@keyframes fade-in
{
    0%
    {
        opacity: 0;
    }
    100%
    {
        opacity: 1;
    }
}
*
{
    margin: 0;
    padding: 0;
}

body{
    background-color: var(--bg_color);
    color: var(--color);
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    max-height: 100vh;
    max-width: 100vw;
}

.layout
{
    display: flex;
    flex-wrap: wrap;
    align-content: center;
    justify-content: space-around;
    flex-direction: column;
    height: 100%;
    min-height: 100vh;
    max-width: 100vw;
    margin: 0;
    text-align: center;
    width: 100%;
}

.layout h2
{
    font-size: 4em;
    padding-top: 5vh;
    text-transform: uppercase;
}

.block{
    text-align: left;
    padding: 1vh 0;
}

.blockTitle
{
    font-size: 1em;
    font-weight: bold;
}

.blockContent
{
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    padding: 20px;
}

.blockElement
{
    opacity: 0.7;
    display: block;
    padding: 10px;
    background-color: gainsboro;
    color: black;
    border-radius: 10%;
    margin: 2.5px;
}

.blockElement:hover,.blockElement:active
{
    cursor: pointer;
    opacity: 1;
}

.blockElement.now
{
    background-color: greenyellow;
}

.blockElement.past
{
    background-color: gainsboro;
}

.blockElement.recent
{
    background-color: orange;
}
.topwork.layout .content
{
    padding: 2vw;
}

.topwork.layout .content >*
{
    margin: 5vh auto;
}
.getconnected
{
    background-image: url(/static/media/connect.bd5f82fc.svg);
    background-repeat: no-repeat;
    background-position: bottom 5vw right 5vw;
    background-size: 40% 40%;
    background-attachment: scroll;
}

.getconnected.layout
{
    justify-content: flex-start;
}

.getconnected.layout .content
{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    margin-top: 10vh;
    padding-left: 2vw;
    align-self: flex-start;
    text-align: left;
}

.getconnected.layout .content>*
{
    padding: 0.5vh;
}

.getconnected.layout .content>*:hover
{
    font-weight: bold;
}

.getconnected.layout .content div
{
    font-size: larger;
}

.getconnected.layout .content div a, a:active, a:hover
{
    color: inherit;
    text-decoration: none;
}

.getconnected.layout .content div img
{
    min-width: 28px;
    max-width: 28px;
    width: 2vw;
}
.about
{
    background-color: var(--bg_color_alt);
    background-attachment: scroll;
    color: var(--color_alt);
}

.about.layout .content
{
    display: flex;
    flex-wrap: wrap;
    padding: 5%;
    text-align: left;
}

 .about.layout .content
{
    text-align: justify;
}

 .about.layout .content ul
{
    padding-left: 3%;
}
